





















































import { Component, Vue, Prop } from "vue-property-decorator";
import { Task } from "@/types";
@Component
export default class TaskCard extends Vue {
  @Prop()
  public index?: number;
  @Prop()
  public task?: Task;
  public startMenu = false;
  public endMenu = false;
  public close() {
    this.$emit("close");
  }
  public submit() {
    this.$emit("submit");
  }
}
